html {
  font-size: 16px;
  font-family: Helvetica, Arial, sans-serif;
}

h2 {
  font-size: 1rem;
}

body {
  margin: 0 0 50px;

  text-align: center;

  background-color: white;

  -webkit-transition: background-color 0.2s, color 0.2s;

  transition: background-color 0.2s, color 0.2s;

  -webkit-user-select: none;

     -moz-user-select: none;

      -ms-user-select: none;

          user-select: none;
}

.night {
  color: white;

  background-color: #242f3e;
}

.cb_wrapper {
    display: -webkit-flex;
    display: flex;
    float: left;
    /* fallback for IE */
    width: 8em;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: 2em;

    margin: 5px;

    border: 1px solid rgba(170, 170, 170, 85);
    border-radius: 1em;

    cursor: pointer;

}

.cb_unchecked-mark, .cb_checked-mark {
    box-sizing: border-box;
    width: 1.2em;
    height: 1.2em;
    margin: 0.4em;
    overflow: hidden;

    border: 1px solid currentColor;
    border-radius: 0.6em;

    color: transparent;

    -webkit-transition: background-color 0.5s;

    transition: background-color 0.5s;
}

.cb_checked-mark {
    background-color: currentColor;
}

.cb_checked-mark .icon {
    top: 0;
}

.cb_label {
    margin: 0.5em 0.5em 0.5em 0;

    font-weight: normal;
}

.icon {
    position: relative;
    top: -2em;

    width: 0.7em;
    height: 0.7em;
    margin: 0.2em;

    color: inherit;

    -webkit-transition: top 0.5s;

    transition: top 0.5s;
}
.crt_wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: stretch;
            justify-content: stretch;
    -webkit-align-items: center;
            align-items: center;
    float: left;
    box-sizing: border-box;
    width: 100%;
    height: 100vh;
    margin: 0;
    padding: 4vh 4vw;
}

.ctr_chart {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    box-sizing: border-box;
    width: 100%;


    -webkit-transform: scaleY(-1);


            transform: scaleY(-1);
}

.crt_controls {
    margin-top: 20px;
}

.chart-line {
    stroke-width: 2.5px;

    -webkit-transition: opacity 0.5s;

    transition: opacity 0.5s;
}

.map-line {
    stroke-width: 1.5px;

    -webkit-transition: opacity 0.5s;

    transition: opacity 0.5s;
}

.chart-bar {
    -webkit-transition: fill 0.5s;
    transition: fill 0.5s;
}

.map-bar {
    -webkit-transition: fill 0.5s;
    transition: fill 0.5s;
}

.animated {
    -webkit-transition: -webkit-transform 0.5s;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
}

.intersection-point {
    -webkit-transition: opacity 0.5s;
    transition: opacity 0.5s;

    stroke-width: 2.5px;
}

.intersection-line {
    opacity: 0.2;

    -webkit-transition: stroke 0.5s;

    transition: stroke 0.5s;
}

.map-chart {
    width: 100%;
    height: 100%;

    -webkit-transform: scaleY(-1);

            transform: scaleY(-1);

}

.map-svg-wrapper {

    height: 100%;
    overflow: hidden;

}

.map-container {

    position: relative;

    width: calc(100% - 20px);
    height: 3em;
    margin-top: 20px;
}

.map-window {
    position: absolute;
    top: -2px;

    box-sizing: border-box;
    height: calc(3em + 4px);

    border-width: 2px;
    border-style: solid none;

    border-color: #c0d1e1;

    -webkit-transition: border-color 0.2s;

    transition: border-color 0.2s;
}

.night .map-window {
    border-color: rgb(86, 98, 109);
}

.map-overlay {
    -webkit-transition: fill 0.2s;
    transition: fill 0.2s;

    fill: rgba(230, 240, 250, 0.6);
}

.night .map-overlay {
    fill: #41557099;
}

.map-left-edge {
    border-left-style: solid;
    border-radius: 10px 0 0 10px;
}

.map-right-edge {
    border-right-style: solid;
    border-radius: 0 10px 10px 0;
}

.map-left-edge, .map-right-edge {
    position: absolute;
    top: -2px;

    box-sizing: border-box;

    width: 10px;
    height: calc(3em + 4px);

    border-width: 1px;
    border-color: #fff;

    background-color: #c0d1e1;

    -webkit-transition: background 0.2s, border-color 0.2s;

    transition: background 0.2s, border-color 0.2s;
}

.night .map-left-edge,
.night .map-right-edge {
    border-color: rgb(86, 98, 109);

    background: rgb(86, 98, 109);
}
.tt_wrapper {
    -webkit-transition: opacity 0.2s, -webkit-transform 0.2s;
    transition: opacity 0.2s, -webkit-transform 0.2s;
    transition: transform 0.2s, opacity 0.2s;
    transition: transform 0.2s, opacity 0.2s, -webkit-transform 0.2s;
}

.tt_rect {
    fill: white;
    stroke: gray;

}

.tt_bold {
    font-weight: bold;
}
.grid_line {
    stroke: gray;
}

.grid_wrapper {
    -webkit-transition: -webkit-transform 0.5s;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
}

.grid_labels {

}
#btn-switch {
    display: inline-block;

    color: #108be3;

    cursor: pointer;

}

#footer {
    clear: both;
}
